import React from "react";
import styled from "styled-components";
import TeamList from "./TeamList/TeamList";
import Media from 'react-media';
import SmallTeamMember from "./TeamList/TeamMember/SmallTeamMember";

const Wrapper = styled.div`
  width: 80%;
  height: auto;
  margin: auto;
  overflow: auto;
`;

const Header = styled.h1`
  text-align: left;
  color: #FC8102;
`

const Team = (props) => {
  return (

  <Media queries={{small: {maxWidth: 900}}}>
    {matches =>
      matches.small ? (
        <Wrapper>
          <Header>Team (Click on Profiles)</Header>
          <TeamList/>
        </Wrapper>
      ) : (
        <Wrapper>
          <Header>Team</Header>
          <TeamList/>
        </Wrapper>
      )
    }

  </Media>
  )

}

export default Team;